    description: `Seorang warga berkostum badut coba menghibur pengendara yang berhenti di persimpangan lampu merah Jalan Majapahit, Yogyakarta. Minggu (24/10/2021)\n\nKata "Pahlawan" tidak hanya diartikan untuk orang-orang yang berjuang melawan penjajah, tidak hanya diartikan untuk orang-orang yang memiliki jabatan tinggi, dan tidak pula hanya diartikan untuk orang-orang yang memiliki prestasi luar biasa dalam mewakili negara.\n\nMenjadi pahlawan tidak selalu dengan berada di posisi terdepan untuk melindungi banyak orang, melainkan melindungi diri sendiri, memenuhi kebutuhan hidup untuk keluarga di tengah tekanan ekonomi akibat pandemi, dan memperoleh kebahagiaan untuk diri sendiri sudah dapat dikatakan sebagai pahlawan.\n\nHal sekecil apapun yang dapat mengubah raut wajah sedih menjadi tawa yang dapat memancarkan sinarnya, juga dapat dikatakan sebagai pahlawan. So, don't stop learning how to be a hero for yourself and shine your kind of light on others`,
