    title: "Bahadur",
    description: `Menilik ke belakang, menjadi seorang perempuan berarti perjuangan. Melawan opresi, diskriminasi, misogini, lalu terpaksa mencari cara berselaras dalam dinamika masyarakat yang didominasi laki-laki. Jangankan menuntut apresiasi, hak bicara pun belum tentu diamini.\n\nKartini dan pahlawan-pahlawan emansipasi membukakan jalan bagi perempuan supaya punya kursi di sekolah hingga pemerintahan. Kini banyak perempuan yang menembus stereotip dan membuktikan bahwa batasan atas apa yang bisa dan tidak bisa dilakukan perempuan di masyarakat hanya imaji semata; diciptakan oleh orang-orang yang takut pada gebrakan.\n\nSekarang perempuan sudah mendapat banyak pengakuan. Stigma makin memudar dan perdebatan mengenai kodrat wanita dan keinginan bekerja tidak lagi signifikan. Karena memilih keduanya atau tidak memilih juga pilihan, bukan?\n\nWalau zaman terus berubah dan dari sejuta jalan yang ditawarkan, perempuan harus selalu punya satu tujuan: kebebasan. Perempuan-perempuan Indonesia telah merdeka. Kini dan selamanya.`,
