    description: `Pada masa pandemi Covid-19 tidak mudah bagi sektor yang terdampak dapat bangkit kembali dan subyeknya tetap berdaya. Kemudian muncul istilah “work from home” dalam berbagai lini sebagai upaya mencegah penyebaran virus. Namun istilah ini tidak dikenal oleh profesi petani. Tetap menjalani aktivitas seperti biasa, membuat November menjadi bulan yang special setiap tahunnya untuk segelintir petani kacang tanah di Dusun Triharjo, Sleman. Setelah tiga bulan penantian, hari panen pun datang ditengah musim penghujan.  Bukan hal yang mengagetkan bagi para petani, penjualan hasil keringat tiga bulan tidak dapat menutup kebutuhan sehari hari. Dengan mendapatkan 6000 rupiah per satu kilogram kacang dan mendapat sekitar 40 kilogram untuk satu karung, membuat para petani harus memutar otak agar uang hasil penjualan dapat menjadi modal menanam bibit padi sebagai jadwal penanaman tiga bulan berikutnya. Mereka juga tetap harus memikirkan keluarga mereka dirumah akan makan apa selama tiga bulan kedepan menunggu masa panen berikutnya. Kondisi ini tidak selaras dengan pengaruh dan dampak yang diberikan petani terhadap masyarakat dan negara secara luas. Sektor pertanian telah membuktikan mampu menjaga bangsa dan negara, dengan terus memberikan pangan yang cukup dan berkontribusi terhadap pemulihan ekonomi nasional. Namun subyek pada sektor pertanian merupakan profesi yang sangat riskan akan krisis. Sebagai sektor informal profesi petani belum diperhatikan secara optimal. Apabila dilihat lebih dalam, seharusnya menjadi petani merupakan salah satu jalan menuju kesejahteraan yang hakiki.`,
