    description: `Diperlukan kesabaran dan sikap legowo untuk menjadi seorang nelayan.\n\nDi satu musim, ia bisa mendapatkan ikan beratus-ratus banyaknya...\nDi musim lainnya, hanya mendengar geliat ikan-ikan kecil dalam jala saja sudah bersyukur.\nParahnya, suhu bumi yang meningkat akan semakin membuatnya terpuruk.\nCuaca ekstrim yang menantang jiwa nelayan paling pemberani sekalipun akan sulit dibendung.\nDitambah penurunan drastis tangkapan karena ikan-ikan sudah tidak punya rumah yang layak lagi.\n\nNamun, para nelayan di Pantai Depok ini tetap bahu-membahu melipat jala,\nyang sayangnya terus dipenuhi sampah laut hasil makhluk daratan (baca: manusia).\nPeluh lelah tetap dilakoni,\npelayaran semalam suntuk hingga pagi buta,\ndemi sesuap harapan.\n\nSeganas apapun arus ganas laut selatan Jawa,\nsebanyak apapun puluhan ton sampah pesisir dan di laut,\nsemua wajib dilalui.\n\nDemi istri, anak, dan masa depan.\n\nDemi kelangsungan hidup keluarga yang menunggu di rumah.`,
