    faculty: "Geografi",
    title: "Sudahkah Menjadi Pahlawan Bagi Diri Sendiri?",
    description: `Pahlawan seringkali didefinisikan sebagai seseorang yang rela berkorban dan berjuang untuk membela kebenaran. Hal tersebut tentunya selalu diidentikan dengan sosok bertopeng yang misterius atau pahlawan nasional yang memperjuangkan kemerdekaan negeri ini. Namun, apakah kita pernah terbesit dipikiran kita untuk menjadi pahlawan bagi diri kita sendiri?\n\nSelama masa pandemi Covid-19, social dan physical distancing membuat kita berjarak satu sama lain. Hingga akhirnya kita semakin merasa sendiri dan memantik pikiran-pikiran yang menjerumuskan kita ke dalam jurang depresi. Kesendirian dan kesepian yang kita alami membuat kita mencoba untuk menghubungkan kembali diri kita dengan orang lain di luar sana. Keterhubungan tersebut yang seringkali membuat kita terlalu fokus terhadap kehidupan orang lain dan mengesampingkan diri kita sendiri. Mencoba untuk memiliki banyak teman dengan tidak menjadi diri sendiri dan terlalu banyak memberi agar orang tersebut tetap disisi. Jika manusia diibaratkan sebagai suatu gelas, bagaimana bisa kita mengisi gelas milik orang lain apabila gelas milik kita sendiri kosong?\n\nKebanyakan dari kita terlalu sering mendengarkan curahan hati orang lain sampai lupa untuk mendengarkan keinginan dan kebutuhan diri sendiri. Oleh karena itu, kita juga perlu untuk menjadi pahlawan bagi diri kita sendiri dengan cara membahagiakan diri sebelum berbagi kebahagiaan itu dengan orang lain. Kebahagiaan akan datang apabila kita mulai menerima diri kita dan melakukan apa yang kita inginkan dan kita butuhkan tanpa harus khawatir atas respon orang lain terhadap diri kita selama hal yang kita lakukan tidak merugikan orang lain.\n\nDengan begitu, menjadi pahlawan bagi diri sendiri seharusnya bisa kita prioritaskan tanpa harus berhenti untuk membantu orang lain. Asal kita tau kapan bisa menjadi pahlawan bagi orang lain dan kapan bisa menjadi pahlawan bagi diri kita sendiri maka akan mendatangkan kebahagiaan bagi diri kita. Mulailah memerdekakan diri kita sendiri dari berbagai macam tuntutan dan standar hidup orang lain agar kita bisa hidup dengan kebahagiaan yang kita buat sendiri.`,
