    description: `Pagi, ketika mentari belum benar-benar menampakkan wujudnya. Oranye mengibaskan gagang sapunya. Dedaunan berjatuhan dimana-mana, Oranye pun datang kesana. Tangannya terus bergerak dan bergerak, tak jarang berhenti sejenak pertanda tangan mulai penat. "Srok... srok... srok..." Suara khasnya beredar di sekeliling kota. Kota yang tadinya kotor sudah bersih kembali. Diangkut keranjang sampah di punggung. Pertanda tugas Oranye hari itu sudah selesai.\nIni adalah sosok pahlawanku. Nama mereka Oranye. Kebanyakan orang kurang mempedulikan mereka. Apa sih mereka, cuma nyapu-nyapu doang. Pahlawan itu kan kayak guru, dokter, polisi, tentara, dsb. Loh, emang cuma konglomerat yang boleh jadi presiden? Emang pekerjaan high class aja yang boleh jadi pahlawan? Emang sih pekerjaannya remeh, tapi peran mereka penting, meskipun selalu tertutupi oleh pekerjaan esensial yang lain. Apakah jika gak ada mereka kota akan terasa nyaman untuk ditinggali? Apakah kita sempat buat menjaga kebersihan lingkungan kota kita? Padahal kita sibuk mikirin utang dan cicilan yang belum terbayar, episode drama korea yang belum kelar, tugas makalah yang belum kelar, mikirin itulah, inilah.\nSebab itu, aku kagum dengan mereka. Sehat selalu Oranye :)`,
