    name: "Fadhlan Rio Lazuardya",
    faculty: "Filsafat",
    description: `Panas terik yang bergumul di kulit, dingin hujan yang mencekam awak. Tiap hari satu per satu manusia, barang, dan makanan dihantar hingga sampai pada penikmat.\n\nTidak bersenjata selayak pahlawan era kolonial, tidak juga berperang melawan penjajah. Hanya berbekal helm di kepalanya dan berperang melawan hiruk-pikuk kehidupan urban.\n\nMenyampaikan amanah pesanan sebagai perantara bisnis kecil pada penikmatnya, mengambil barang atau makanan bagi kita;manusia-manusia penyibuk yang pikun.\n\nKita yang pikun, akan kesadaran peran andil pengemudi ojek online dewasa ini. Mereka membantu menggerakkan aktivitas urban manusia yang semakin modern.\nKontribusi kecil, tetapi menonggak titik mobilisasi masif. Menunjang usaha perekonomian di masa pandemi.\n\nBahkan, di jalanan para pengemudi ojek online menghadapi realita resiko celaka, pesanan fiktif, dan resiko lainnya tanpa kita ketahui ada asa yang diperjuangkan untuk rumah milik mereka.`,
