    description: `As an everyday heroes, the gentleman in the picture might not be very obvious on how is he a hero. But let me assure you guys that he is, at least he is my everyday hero.\n\nIn January 2020, I was strolling around Kota Lama and then I ran into this gentleman in his antique shop.  His passion in teaching others on antique goods, he provides second chances for the goods to be revamped by a new owner and ensures that their memories will stay intact. He keeps the history of decades prior, those years that we’ve never experienced before, alive.\n\nFor me is not that he sell antique goods that makes him a hero, it's how the passion and will that inspire me to put 110% in everything that I do, as I'm sure how he has inspire many other people that stopped by his shop.`,
